$primary: rgba(79, 70, 186, 1);
$BrightRed: rgba(249, 137, 107, 1);
$Sand: rgba(253, 188, 100, 1);
$Blue: rgba(55, 52, 169, 1);
$DarkGrey: rgb(102, 102, 109);
$lightkGrey: rgba(185, 184, 184, 0.3);

h1 {
    color: red($color: #000000);
}

.testimonial {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: url(../../images/testimonial_watermark.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    min-height: 60vh;
    text-align: center;
    padding: 20px;
    // border: 1px solid blue;

    @media screen and (min-width:920px) {
        min-height: 85vh;
    }


    .headContainer {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (max-width:920px) {
            margin-bottom: -30px;
        }

        .title {
            color: $primary;
        }

        .head {
            font-size: clamp(1.6rem, 3.1vw, 4rem);
            margin-bottom: 60px;
            max-width: 550px;

            @media screen and (min-width:920px) {
                margin-bottom: 50px;
            }

        }
    }

    .name {
        font-size: .8rem;
        color: $DarkGrey;
        text-transform: capitalize;
        font-weight: bold;
        margin-top: 50px;
    }

    .body {
        font-size: 1.1rem;
        color: $DarkGrey;
        line-height: 1.8rem;
        height: 80px;
        overflow: hidden;
        margin-bottom: 20px;

    }

    .email {
        font-size: .75rem;
    }

    .reviewsContainer {
        padding: 0 20px;


        .reviewCard {
            background-color: white;
            border: 1px solid $lightkGrey;
            border-radius: 5px;
            max-width: 300px;
            max-height: 320px;
            padding: 20px;
            margin: auto;
            text-align: left;
            margin-bottom: 35px;
            box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.1);
            -webkit-box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.1);
            -moz-box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.1);
            position: relative;
            transform: scale(.9);

            // @media screen and (min-width:920px) {

            //     transform: scale(.9);
            // }

            position: relative;

            &::after {
                content: "";
                height: 1px;
                width: 100%;
                background-color: $lightkGrey;
                position: absolute;
                bottom: 85px;
                left: 0;
            }

            .starCon {
                display: flex;
                margin: 30px 0 15px 0;
            }

            .quote {
                background-color: $Blue;
                font-size: 2.5rem;
                color: white;
                padding: 12px;
                border-radius: 50%;
                position: absolute;
                top: -15px;
                left: 30px;


                @media screen and (min-width:920px) {
                    font-size: 3rem;
                }
            }
        }

    }

    @media screen and (min-width:920px) {
        padding: 0 70px;

        .testimonial {
            .head {
                margin-bottom: 0px;
            }

            // display: flex;
        }
    }

    // .sliderContainer {
    //     background-color: blue;

    // }

    .slick-slide>div {
        margin: 0 10px;
        padding-top: 20px;
        max-width: 300px;

        @media screen and (max-width:920px) {
            margin: 0 10px -40px 10px;

        }

    }

    .slick-list {
        margin: 0 60px;

        @media screen and (max-width:920px) {
            margin: 0 40px;
        }
    }
}

.slick-prev:before,
.slick-next:before {


    color: $Blue  !important;
}