a {
  cursor: pointer;
}

.Navshadow {
  background-color: blue;
  box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 3px 11px 8px -8px rgba(0, 0, 0, 0.2);
}

.navBar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  margin-inline: auto;
  background-color: white;
  padding: 5px 150px 25px 150px;
  z-index: 30;
}
@media screen and (max-width: 1000px) {
  .navBar {
    width: 100%;
    padding: 0px 30px 25px 30px;
  }
}
.navBar .Navlinks,
.navBar .Seclinks {
  display: flex;
}
.navBar li {
  padding: 8px 18px;
  border-radius: 20px;
}
.navBar li:active .navBar {
  display: none;
}
.navBar a {
  font-size: 1rem;
  font-weight: 500;
}
.navBar .logo {
  font-size: 2rem;
  color: rgb(79, 70, 186);
  text-transform: lowercase;
  z-index: 80000;
}
@media screen and (max-width: 920px) {
  .navBar .logo {
    font-size: 1.2rem;
  }
  .navBar .logo .whiteLogo {
    color: white;
    padding-left: 10px;
  }
}
.navBar .logo span {
  color: red;
}
.navBar .container_nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 5rem;
  position: relative;
}
@media screen and (max-width: 920px) {
  .navBar .container_nav {
    flex-direction: column;
    width: 100%;
    margin-left: -1000px;
    position: absolute;
    top: -30px;
    left: -2px;
    gap: 0rem;
    align-items: center;
    text-align: left;
    background-color: rgb(55, 52, 169);
    padding: 70px 0 40px 0;
    height: 100vh;
    transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .navBar .container_nav .Navlinks,
.navBar .container_nav .newUser {
    flex-direction: column;
    gap: 0rem;
  }
  .navBar .container_nav li {
    padding: 10px 20px;
  }
  .navBar .container_nav li:hover {
    background-color: white;
    color: rgb(55, 52, 169);
  }
  .navBar .container_nav li:hover a {
    color: rgb(55, 52, 169);
  }
  .navBar .container_nav li a {
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
  }
}
.navBar .container_nav .newUser .Seclinks {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.navBar .container_nav .newUser .Seclinks li:nth-child(2) {
  background-color: rgb(55, 52, 169);
}
.navBar .container_nav .newUser .Seclinks li:nth-child(2) a {
  color: white;
}
.navBar .container_nav .newUser .Seclinks li:hover > .Seclinks {
  background-color: red;
}
@media screen and (max-width: 920px) {
  .navBar .container_nav .newUser .Seclinks {
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
  }
  .navBar .container_nav .newUser .Seclinks li:nth-child(2) {
    background-color: white;
  }
  .navBar .container_nav .newUser .Seclinks li:nth-child(2) a {
    color: rgb(55, 52, 169);
  }
}
.navBar .hide {
  display: none;
}
.navBar .hideOnMobile {
  margin-left: 0;
}
@media screen and (min-width: 920px) {
  .navBar .hideOnMobile {
    display: flex;
    transition: all 0.5s cubic-bezier(0, 1, 0, 1);
  }
}
.navBar .toggleBtn {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  z-index: 400;
}
@media screen and (max-width: 920px) {
  .navBar .toggleBtn {
    display: block;
  }
}
.navBar #close {
  color: white;
}
.navBar .secAbout {
  display: none;
  background-color: rgb(55, 52, 169);
  position: absolute;
  left: 0;
  padding: 5px 0;
  width: 120px;
  margin-top: 18px;
  transition: all 0.5s ease-in;
  text-align: center;
}
.navBar .secAbout::after {
  content: "";
  background-color: rgb(55, 52, 169);
  height: 40px;
  width: 40px;
  position: absolute;
  transform: rotate(45deg);
  left: 35%;
  top: 0;
  z-index: -1;
}
.navBar .secAbout li:first-child {
  border-bottom: 1px solid rgb(79, 70, 186);
  width: 100%;
  border-radius: 0;
  margin-bottom: 5px;
}
.navBar .secAbout li a {
  color: white;
}
.navBar .parentList:hover > .secAbout {
  display: block;
  margin-top: 15px;
  transition: all 0.5s ease-in;
}
.navBar Link {
  cursor: pointer;
}/*# sourceMappingURL=TopNav.css.map */